import React, {useState} from 'react'

export default function TextForm(props) {
    const handelUpClick = ()=>{
        // console.log('upper case clicked'+text);
        let newText = text.toUpperCase();
        setText(newText);
        if(newText.length>0){
            props.showAlert("Converted to Upper Case", "success");
        }
        
    }

    const handelLoClick = () =>{
        let newText = text.toLowerCase();
        setText(newText);
        if(newText.length>0){
        props.showAlert("Converted to Lower Case", "success");
        }
    }

    const handelClearClick = () => {
        if(text.length>0){
            props.showAlert("Text has been removed", "success");
        }
        let newText = '';
        setText(newText);
    }
    const handelOnChange = (event) =>{
        // console.log('handel on change');
        setText(event.target.value);
    }
   

    const [text, setText] = useState("");   
    return (
        <>
            <div className = "container" style= {{color: props.mode==='dark'?'white':'black'}}>
                <h1 style = {{color: props.mode==='dark'?'white' : 'black'}}>{props.heading}</h1>
                <div className="mb-3">
                    <textarea className="form-control" value= {text}  onChange = {handelOnChange} style = {{background: props.mode==='dark'?'#042743': 'white', color: props.mode==='dark'?'white':'black'}} id="myBox" rows="8"></textarea>
                </div>
                <button className = "btn btn-primary mx-1" onClick= {handelUpClick} >Convert to UpperCase</button>
                <button className = "btn btn-primary mx-1" onClick= {handelLoClick} >Convert to LowerCase</button>
                <button className = "btn btn-primary mx-1" onClick= {handelClearClick} >Clear Text</button>
            </div>
            <div className = "container my-3" style= {{color: props.mode==='dark'?'white':'black'}}>
                <h2> Your text summary</h2>
                <p>{text.split(" ").length} words and {text.length} characters.</p>
                <h2>Preview</h2>
                <p>{text}</p>
            </div>
        </>
    )
}
